<template>
  <div>
    <Form :label-width="140" :label-colon="true" ref="form" :model="form" :rules="rule" style="max-width: 600px; margin: 40px auto 0">
      <FormItem prop="paymentUser" label="付款账户">
        <Select v-model="form.paymentUser" placeholder="选择付款账户">
          <Option value="1">admin@iview.design</Option>
        </Select>
      </FormItem>
      <FormItem prop="payType" label="收款账户">
        <Input v-model="form.payType" placeholder="输入收款账户">
          <template #prepend>
            <Select v-model="form.defType" style="width: 80px">
              <Option value="alipay">支付宝</Option>
              <Option value="wexinpay">微信</Option>
            </Select>
          </template>
        </Input>
      </FormItem>
      <FormItem prop="name" label="收款人姓名">
        <Input v-model="form.name" placeholder="输入收款人姓名" />
      </FormItem>
      <FormItem prop="money" label="转账金额">
        <Input v-model="form.money" prefix="logo-yen" placeholder="输入转账金额" />
      </FormItem>
      <FormItem>
        <Button type="primary" @click="nextStep">下一步</Button>
      </FormItem>
    </Form>
    <Divider />
    <div class="step-form-style-desc">
      <h3>说明</h3>
      <h4>转账到支付宝账户</h4>
      <p>
        如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。
      </p>
      <h4>转账到银行卡</h4>
      <p>
        如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。如果需要，这里可以放一些关于产品的常见问题说明。
      </p>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'Step1',
    data() {
      return {
        form: {
          paymentUser: '1',
          defType: 'alipay',
          payType: 'user@iview.design',
          name: 'Alex',
          money: '5000'
        },
        rule: {
          paymentUser: [{ required: true, message: '付款账户必须填写', trigger: 'blur' }],
          payType: [{ required: true, message: '收款账户必须填写', trigger: 'blur' }],
          name: [{ required: true, message: '请输入收款人姓名', trigger: 'blur' }],
          money: [{ required: true, message: '转账金额必须填写', trigger: 'blur' }]
        }
      }
    },
    methods: {
      nextStep() {
        // 先校验，通过表单校验后，才进入下一步
        this.$refs.form.validate(valid => {
          if (valid) {
            this.$emit('nextStep')
          }
        })
      }
    }
  }
</script>

