<template>
  <div>
    <Form :label-width="140" :label-colon="true" ref="form" :model="form" :rules="rule" style="max-width: 600px; margin: 40px auto 0">
      <Alert show-icon closable style="margin-bottom: 24px">确认转账后，资金将直接打入对方账户，无法退回。</Alert>
      <FormItem label="付款账户">admin@iview.design</FormItem>
      <FormItem label="收款账户">user@iview.design</FormItem>
      <FormItem label="收款人姓名">Alex</FormItem>
      <FormItem label="转账金额">￥ 5,000.00</FormItem>
      <Divider />
      <FormItem prop="paymentPassword" label="支付密码">
        <Input v-model="form.paymentPassword" type="password" placeholder="输入支付密码" />
      </FormItem>
      <FormItem>
        <Button :loading="loading" type="primary" @click="nextStep">提交</Button>
        <Button style="margin-left: 8px" @click="prevStep">上一步</Button>
      </FormItem>
    </Form>
  </div>
</template>

<script>
  export default {
    name: 'Step2',
    data() {
      return {
        labelCol: { lg: { span: 5 }, sm: { span: 5 } },
        wrapperCol: { lg: { span: 19 }, sm: { span: 19 } },
        form: {
          paymentPassword: '123456'
        },
        rule: {
          paymentPassword: [{ required: true, message: '请输入支付密码', trigger: 'blur' }]
        },
        loading: false,
        timer: 0
      }
    },
    methods: {
      nextStep() {
        this.loading = true
        this.$refs.form.validate(valid => {
          if (valid) {
            this.timer = setTimeout(() => {
              this.loading = false
              this.$emit('nextStep')
            }, 1500)
          } else {
            this.loading = false
          }
        })
      },
      prevStep() {
        this.$emit('prevStep')
      }
    },
    beforeUnmount() {
      clearTimeout(this.timer)
    }
  }
</script>
