<template>
  <PageView :content="content">
    <div>
      <Card class="ivu-card" title="出差信息" shadow>
        <on-a-business-trip-form ref="onABusinessTrip" />
      </Card>
      <Card class="ivu-card" title="行程信息" shadow>
        <trip-form ref="trip" />
      </Card>

      <!-- table -->
      <Card title="同行人员" shadow>
        <Table :columns="columns" :data="data" :loading="memberLoading">
          <template v-for="(column, index) in ['name', 'workId', 'department']" #[column]="{ row }">
            <Input
              v-if="row.editable"
              :key="column"
              :modelValue="row[column]"
              :placeholder="columns[index + 1].title"
              @on-change="event => handleChange(event.target.value, row.key, column)"
            />
            <template v-else>{{ row[column] }}</template>
          </template>
          <template #action="{ row }">
            <template v-if="row.editable">
              <template v-if="row.isNew">
                <Space>
                  <Button type="success" size="small" ghost @click="saveRow(row)">添加</Button>
                  <Button type="error" size="small" ghost @click="remove(row.key)">删除</Button>
                </Space>
              </template>
              <template v-else>
                <Space>
                  <Button type="success" size="small" ghost @click="saveRow(row)">保存</Button>
                  <Button type="primary" size="small" ghost @click="cancel(row.key)">取消</Button>
                </Space>
              </template>
            </template>
            <template v-else>
              <Space>
                <Button type="primary" size="small" ghost @click="toggle(row.key)">编辑</Button>
                <Poptip confirm transfer title="您确认删除这条内容吗？" @on-ok="remove(row.key)" @on-cancel="cancel(row.key)">
                  <Button type="error" size="small" ghost>删除</Button>
                </Poptip>
              </Space>
            </template>
          </template>
        </Table>
        <Button type="dashed" icon="md-add" long class="ivu-mt" @click="newMember">新增成员</Button>
      </Card>

      <!-- fixed footer toolbar -->
      <footer-toolbar
        :style="{
          width: isSideMenu && isDesktop ? `calc(100% - ${menuCollapse ? $config.siderCollapseWidth : $config.siderExpandedWidth}px)` : '100%'
        }"
      >
        <Poptip title="表单校验信息">
          <template #content>
            <li v-for="(item, index) in errors" :key="index" @click="scrollToField(item)" class="error-item">
              <Icon type="ios-alert-outline" size="16" class="ivu-font-bold error-icon" />
              <div>{{ item }}</div>
            </li>
          </template>
          <span class="error-icon" v-if="errors.length"> <Icon type="ios-alert-outline" class="ivu-font-bold" size="16" />{{ errors.length }} </span>
        </Poptip>
        <Button type="primary" @click="validate" :loading="loading">提交</Button>
      </footer-toolbar>
    </div>
  </PageView>
</template>

<script>
  import { FooterToolbar } from 'view-ui-plus'
  import TripForm from './TripForm'
  import { PageView } from '@/components'
  import OnABusinessTripForm from './OnABusinessTripForm'
  import { mixinState, mixinDevice } from '@/utils/mixin'

  export default {
    name: 'AdvancedForm',
    mixins: [mixinState, mixinDevice],
    components: {
      OnABusinessTripForm,
      FooterToolbar,
      TripForm,
      PageView
    },
    data() {
      return {
        content: '高级表单常见于一次性输入和提交大批量数据的场景。',
        loading: false,
        memberLoading: false,

        // table
        columns: [
          {
            title: '序号',
            type: 'index',
            align: 'center',
            width: 120
          },
          {
            title: '姓名',
            key: 'name',
            slot: 'name',
            minWidth: 120
          },
          {
            title: '工号',
            key: 'workId',
            slot: 'workId',
            minWidth: 120
          },
          {
            title: '所属部门',
            key: 'department',
            slot: 'department',
            minWidth: 120
          },
          {
            title: '操作',
            slot: 'action',
            align: 'center',
            minWidth: 140
          }
        ],
        data: [
          {
            key: 1,
            name: 'Echo',
            workId: '001',
            editable: false,
            department: '研发部'
          },
          {
            key: 2,
            name: '中小鱼',
            workId: '002',
            editable: false,
            department: '设计部'
          },
          {
            key: 3,
            name: '唐不苦',
            workId: '003',
            editable: false,
            department: '产品部'
          }
        ],

        errors: []
      }
    },
    methods: {
      newMember() {
        const length = this.data.length
        this.data.push({
          key: length === 0 ? 1 : parseInt(this.data[length - 1].key) + 1,
          name: '',
          workId: '',
          department: '',
          editable: true,
          isNew: true
        })
      },
      remove(key) {
        this.data = this.data.filter(item => item.key !== key)
      },
      saveRow(record) {
        this.memberLoading = true
        const { key, name, workId, department } = record
        if (!name || !workId || !department) {
          this.memberLoading = false
          this.$Message.error('请填写完整成员信息。')
          return
        }
        // 模拟网络请求、卡顿 500ms
        new Promise(resolve => {
          setTimeout(() => {
            resolve({ loop: false })
          }, 500)
        }).then(() => {
          const target = this.data.find(item => item.key === key)
          target.editable = false
          target.isNew = false
          this.memberLoading = false
        })
      },
      toggle(key) {
        const target = this.data.find(item => item.key === key)
        target._originalData = { ...target }
        target.editable = !target.editable
      },
      getRowByKey(key, newData) {
        const data = this.data
        return (newData || data).find(item => item.key === key)
      },
      cancel(key) {
        const target = this.data.find(item => item.key === key)
        Object.keys(target).forEach(key => {
          target[key] = target._originalData[key]
        })
        target._originalData = undefined
      },
      handleChange(value, key, column) {
        const newData = [...this.data]
        const target = newData.find(item => key === item.key)
        if (target) {
          target[column] = value
          this.data = newData
        }
      },

      // 最终全页面提交
      validate() {
        const {
          $refs: { onABusinessTrip, trip },
          $Notice
        } = this

        const onABusinessTripErrors = []
        const fields = Object.keys(onABusinessTrip.form)
        fields.pop()
        fields.forEach(field =>
          onABusinessTrip.$refs.form.validateField(field, error => {
            if (error) onABusinessTripErrors.push(error)
          })
        )
        const onABusinessTripForm = new Promise((resolve, reject) => {
          if (onABusinessTripErrors.length) {
            reject(onABusinessTripErrors)
          } else {
            resolve(onABusinessTrip.form)
          }
        })

        const tripErrors = []
        Object.keys(trip.form).forEach(field =>
          trip.$refs.form.validateField(field, error => {
            if (error) tripErrors.push(error)
          })
        )
        const tripForm = new Promise((resolve, reject) => {
          if (tripErrors.length) {
            reject(tripErrors)
          } else {
            resolve(trip.form)
          }
        })

        // clean this.errors
        this.errors = []
        Promise.all([onABusinessTripForm, tripForm])
          .then(values => {
            $Notice.success({
              title: 'Received values of form:',
              desc: JSON.stringify(values)
            })
          })
          .catch(() => {
            this.errors = [...onABusinessTripErrors, ...tripErrors]
          })
      },
      scrollToField(fieldKey) {
        const errorNodes = document.querySelectorAll('div[class="ivu-form-item-error-tip"]')
        for (const error of errorNodes) {
          if (error.innerHTML === fieldKey) {
            error.scrollIntoView(true)
            break
          }
        }
      }
    }
  }
</script>

<style lang="less" scoped>
  .ivu-card {
    margin-bottom: 24px;
  }

  :deep(.ivu-table) {
    &:before,
    th {
      background: #fff;
    }
  }

  :deep(.ivu-poptip-body) {
    padding: 4px;
    min-width: 200px;
    max-height: 280px;
    overflow: auto;
  }

  .error-icon {
    user-select: none;
    margin-right: 24px;
    color: #f5222d;
    cursor: pointer;

    i {
      margin-right: 4px;
    }
  }

  .error-item {
    padding: 8px 10px;
    list-style: none;
    border-bottom: 1px solid #e8e8e8;
    cursor: pointer;
    transition: all 0.3s;

    &:hover {
      background: #e6f7ff;
    }

    .error-icon {
      float: left;
      margin-top: 4px;
      margin-right: 12px;
      color: #f5222d;
    }
  }
</style>
