<template>
  <div>
    <Form label-position="top" :label-colon="true" ref="form" :model="form" :rules="rule">
      <Row :gutter="24">
        <Col :xs="{ span: 24 }" :sm="{ span: 24 }" :md="{ span: 8 }" :lg="{ span: 8 }" :xl="{ span: 8 }">
          <FormItem prop="cityName" label="出发城市">
            <City v-model="form.cityName" clearable use-name show-suffix placeholder="请选择出发城市" />
          </FormItem>
        </Col>
        <Col :xs="{ span: 24 }" :sm="{ span: 24 }" :md="{ span: 8 }" :lg="{ span: 8 }" :xl="{ span: 8 }">
          <FormItem prop="timeOfDeparture" label="出发时间">
            <DatePicker
              v-model="form.timeOfDeparture"
              type="datetime"
              confirm
              format="yyyy-MM-dd HH:mm"
              style="width: 100%"
              placeholder="请选择出发时间"
            />
          </FormItem>
        </Col>
        <Col :xs="{ span: 24 }" :sm="{ span: 24 }" :md="{ span: 8 }" :lg="{ span: 8 }" :xl="{ span: 8 }">
          <FormItem prop="returnTime" label="返程时间">
            <DatePicker
              v-model="form.returnTime"
              type="datetime"
              confirm
              format="yyyy-MM-dd HH:mm"
              style="width: 100%"
              placeholder="请选择返程时间"
            />
          </FormItem>
        </Col>
        <Col :xs="{ span: 24 }" :sm="{ span: 24 }" :md="{ span: 8 }" :lg="{ span: 8 }" :xl="{ span: 8 }">
          <FormItem prop="trafficTypes" label="交通类型">
            <Select v-model="form.trafficTypes" placeholder="请选择交通类型">
              <Option value="1">火车</Option>
              <Option value="2">飞机</Option>
            </Select>
          </FormItem>
        </Col>
      </Row>
    </Form>
  </div>
</template>

<script>
  import { City } from '@/components'

  export default {
    name: 'TripForm',
    components: { City },
    data() {
      return {
        form: {
          cityName: '',
          timeOfDeparture: '',
          returnTime: '',
          trafficTypes: ''
        },
        rule: {
          cityName: [{ required: true, message: '请选择出发城市', trigger: 'change' }],
          tripReasons: [{ required: true, message: '请填写出差事由', trigger: 'blur' }],
          timeOfDeparture: [
            {
              required: true,
              message: '请选择出差时间',
              trigger: 'change',
              pattern: /.+/
            }
          ],
          returnTime: [
            {
              required: true,
              message: '请选择返程时间',
              trigger: 'change',
              pattern: /.+/
            }
          ],
          trafficTypes: [{ required: true, message: '请选择交通工具', trigger: 'change' }]
        }
      }
    },
    methods: {}
  }
</script>

<style scoped></style>
