<template>
  <PageView :content="content">
    <Card shadow>
      <Row>
        <Col
          :xs="{ span: 24 }"
          :sm="{ span: 20, offset: 2 }"
          :md="{ span: 16, offset: 4 }"
          :lg="{ span: 12, offset: 6 }"
          :xl="{ span: 12, offset: 6 }"
        >
          <Form :label-width="140" :label-colon="true" ref="form" :model="form" :rules="rule">
            <FormItem prop="title" label="会议名称">
              <Input v-model="form.title" placeholder="会议的名称" />
            </FormItem>
            <FormItem prop="content" label="会议内容">
              <Input v-model="form.content" placeholder="会议的内容" />
            </FormItem>
            <FormItem prop="date" label="起止时间">
              <DatePicker v-model="form.date" style="width: 100%" type="datetimerange" placeholder="选择会议的起止时间" />
            </FormItem>
            <FormItem prop="room" label="会议地点">
              <Select v-model="form.room" placeholder="请选择会议室">
                <Option value="1">1号会议室</Option>
                <Option value="2">2号会议室</Option>
                <Option value="3">3号会议室</Option>
                <Option value="4">4号会议室</Option>
                <Option value="5">5号会议室</Option>
              </Select>
            </FormItem>
            <FormItem label="参会者（选填）" prop="users">
              <Select v-model="form.users" multiple placeholder="请选择参会人员">
                <Option value="逢跪跪">逢跪跪</Option>
                <Option value="中小鱼">中小鱼</Option>
                <Option value="唐不苦">唐不苦</Option>
              </Select>
            </FormItem>
            <FormItem label="备注（选填）" prop="remark">
              <Input v-model="form.remark" :rows="3" type="textarea" placeholder="备注" show-word-limit :maxlength="200" />
            </FormItem>
            <FormItem label="视频支持" prop="video">
              <RadioGroup v-model="form.video">
                <Radio label="1">需要</Radio>
                <Radio label="2">不需要</Radio>
              </RadioGroup>
            </FormItem>
            <FormItem>
              <Button class="ivu-mr-8" type="primary" @click="handleSubmit('form')">提交</Button>
              <Button @click="handleReset('form')">重置</Button>
            </FormItem>
          </Form>
        </Col>
      </Row>
    </Card>
  </PageView>
</template>

<script>
  import { PageView } from '@/components'

  export default {
    name: 'BaseForm',
    components: { PageView },
    data() {
      return {
        content: '表单页用于向用户收集或验证信息，基础表单常见于数据项较少的表单场景。',
        form: {
          title: '',
          content: '',
          date: '',
          room: '',
          users: [],
          remark: '',
          video: '1'
        },
        rule: {
          title: [{ required: true, message: '请输入会议的名称', trigger: 'blur' }],
          content: [{ required: true, message: '请输入会议的内容', trigger: 'blur' }],
          date: [
            {
              required: true,
              message: '选择会议的起止时间',
              trigger: 'change',
              pattern: /.+/
            }
          ],
          room: [{ required: true, message: '请选择会议室', trigger: 'change' }]
        },
        form1: {
          name: '',
          date: '',
          description: '',
          type: '',
          customer: '',
          inviteCritics: '',
          weight: undefined,
          value: 1
        },
        rule1: {
          name: [{ required: true, message: '请输入标题' }],
          date: [{ required: true, message: '请选择起止日期', pattern: /.+/ }],
          description: [{ required: true, message: '请输入目标描述' }],
          type: [{ required: true, message: '请输入衡量标准' }],
          customer: [{ required: true, message: '请描述你服务的客户' }]
        }
      }
    },
    methods: {
      handleSubmit(name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            this.$Message.success('Success!')
          } else {
            this.$Message.error('Fail!')
          }
        })
      },
      handleReset(name) {
        this.$refs[name].resetFields()
      }
    }
  }
</script>
